import React, { useCallback } from 'react';
import EmbedFileCardWrapper from '../embedFileCardWrapper';
import { EmbedNode } from '../../../types';
import {
  CardContentWrapper, CardContentInnerWrapper,
  ProgressBar, ProgressBarInner, ContentHeader,
} from './styled';

interface Props {
  node: EmbedNode;
  isSelected: boolean;
  enableBorderHighlight?: boolean;
}

export default function EmbedUploadProgressCard(props: Props) {
  const { node, isSelected, enableBorderHighlight } = props;
  const { uploadPercent, name, errorType } = node.data;

  const getUploadProgress = useCallback(() => {
    return uploadPercent ? uploadPercent * 0.95 : 0;
  }, [uploadPercent]);

  return (
    <EmbedFileCardWrapper
      node={node}
      isUploadFailed={!!errorType}
      isSelected={isSelected}
      enableBorderHighlight={enableBorderHighlight}
    >
      <CardContentWrapper>
        <CardContentInnerWrapper>
          <ContentHeader>{name}</ContentHeader>
        </CardContentInnerWrapper>
        {uploadPercent ? (
          <ProgressBar>
            <ProgressBarInner progress={getUploadProgress()} />
          </ProgressBar>
        ) : null}
      </CardContentWrapper>
    </EmbedFileCardWrapper>
  );
}

